import React, { useState } from "react";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Chip from "@mui/material/Chip";
import "./RightModal.css";
import { get, isEmpty } from "lodash";
import moment from "moment";
import { Drawer } from "@mui/material";
import Button from "@mui/material/Button";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import ReactHtmlParser from 'react-html-parser';

const ServiceplanDetail = (props) => {
	const { open, onClose, dialogData } = props;

	let filterString = get(dialogData, "rowData.details", "");
	// let strippedString = filterString?.replace(/<[^>]+>/g, "");

	const [isFullTextVisible, setIsFullTextVisible] = useState(false);

	function getDisplayText() {
		if (isFullTextVisible) {
			return filterString && filterString;
		} else {
			const words = (filterString && filterString).split(" ");
			const truncatedWords = words.slice(0, 94);
			const truncatedText = truncatedWords.join(" ");
			return truncatedText;
		}
	}

	const parseHtmlContent = (htmlString) => {
		const parser = new DOMParser();
		const parsedHtml = parser.parseFromString(htmlString, 'text/html');
		return parsedHtml.body;
	  };
	
	  const renderHtmlElements = (htmlBody) => {
		const children = [];
	

		for (const childNode of htmlBody.childNodes) {
		  if (childNode.nodeType === Node.TEXT_NODE) {
			children.push(childNode.textContent);
		  } else if (childNode.nodeType === Node.ELEMENT_NODE) {
			children.push(
			  React.createElement(
				childNode.tagName.toLowerCase(),
				{},
				renderHtmlElements(childNode)
			  )
			);
		  }
		}
			
		return children;
	  };

	//   const renderedElements = renderHtmlElements(parseHtmlContent(getDisplayText()));
	
	 

	return (
		<>
			<Drawer
				anchor="right"
				open={open}
				onClose={onClose}
				className="plan-details-design"
			>
				<div className="service-plan-add-header" >
					{/* <Typography variant="h6">Plan Details</Typography> */}
					<Button onClick={onClose}>
						<ArrowBackIosIcon />
					</Button>
					<h2>Plan Details</h2>
				</div>
				<div style={{width:"100%",height:"2px", backgroundColor:"#0000001f"}}></div>
				<div className="plan_details drawer-content">
					<Grid container spacing={2}>
						<Grid item xs={12} sm={12}>
							<Grid container spacing={2}>
								<Grid item xs={5} md={5}>
									<Typography variant="subtitle1" color="text.primary">
										Plan Name
									</Typography>

									<Typography variant="body1">
										{get(dialogData, "rowData.planName", "")}
									</Typography>
								</Grid>

								<Grid item xs={4} md={4}>
									<Typography variant="subtitle1" color="text.primary">
										Consultation Sessions
									</Typography>

									<Box>
										<Chip
											label={get(dialogData, "rowData.sessionName", "")}
											sx={{
												backgroundColor: "secondary.lighter",
												color: "secondary.dark",
											}}
										/>
									</Box>
								</Grid>

								<Grid item xs={3} md={3}>
									<Typography variant="subtitle1" color="text.primary">
										Duration
									</Typography>

									<Typography variant="body1">
										{get(dialogData, "rowData.timeUnit", "")} Min
									</Typography>
								</Grid>

								

								<Grid item xs={5} md={5}>
									<Typography variant="subtitle1" color="text.primary">
										Post Consultation plan (included)
									</Typography>

									<Chip
										label={
											get(dialogData, "rowData.dietPlan", false) ? "Yes" : "No"
										}
										sx={{
											border: "1px solid",
											backgroundColor: "transparent",
											borderColor: "secondary.main",
											color: "secondary.main",
										}}
									/>
								</Grid>

								<Grid item xs={4} md={4}>
									<Typography variant="subtitle1" color="text.primary">
										Service Charges
									</Typography>

									<Typography variant="body1">
										{get(dialogData, "rowData.amount", "")}{" "}
										{get(dialogData, "rowData.currencyId.currencySymbol", "")}
									</Typography>
								</Grid>

								<Grid item xs={3} md={3}>
									<Typography variant="subtitle1" color="text.primary">
										Number of Consultant
									</Typography>

									<Typography variant="body1">
										{get(dialogData, "rowData.numberOfConsultant", "")}
									</Typography>
								</Grid>

								<Grid item xs={5} md={5}>
									<Typography variant="subtitle1" color="text.primary">
										Consultation Type
									</Typography>
									<Typography
										variant="body1"
										style={{ display: "flex", flexDirection: "row" }}
									>
										{dialogData?.rowData?.onlineConsultation && (
											<Box>
												<Chip
													label={"Online"}
													sx={{
														backgroundColor: "secondary.lighter",
														color: "secondary.dark",
														marginRight: "6px",
													}}
												/>
											</Box>
										)}
										{dialogData?.rowData?.offlineConsultation && (
											<Box>
												<Chip
													label={"Offline"}
													sx={{
														backgroundColor: "secondary.lighter",
														color: "secondary.dark",
														marginRight: "6px",
													}}
												/>
											</Box>
										)}
										{dialogData?.rowData?.phoneConsultation && (
											<Box>
												<Chip
													label={"Phone"}
													sx={{
														backgroundColor: "secondary.lighter",
														color: "secondary.dark",
														marginRight: "6px",
													}}
												/>
											</Box>
										)}
									</Typography>
								</Grid>

								<Grid item xs={4} md={4}>
									<Typography variant="subtitle1" color="text.primary">
										Created Date
									</Typography>
									<Box>
										<Chip
											label={
												isEmpty(get(dialogData, "rowData.createDate", ""))
													? "Date Not Found"
													: moment(dialogData.rowData.createDate).format("LL")
											}
											sx={{
												backgroundColor: "secondary.lighter",
												color: "secondary.dark",
											}}
										/>
									</Box>
								</Grid>

								<Grid item xs={3} md={3}>
									<Typography variant="subtitle1" color="text.primary">
										Last Update Date
									</Typography>
									<Box>
										<Chip
											label={
												isEmpty(get(dialogData, "rowData.updateDate", ""))
													? "Not Updated"
													: moment(dialogData.rowData.updateDate).format("LL")
											}
											sx={{
												backgroundColor: "secondary.lighter",
												color: "secondary.dark",
											}}
										/>
									</Box>
								</Grid>
								{/* <Grid item xs={3} md={3}>
									<Typography variant="subtitle1" color="text.primary">
										Time Minutes
									</Typography>

									<Typography variant="body1">
										{get(dialogData, "rowData.timeUnit", "")}
									</Typography>
								</Grid> */}

								<Grid item xs={12} md={12}>
									<Typography variant="subtitle1" color="text.primary">
										Details
									</Typography>
									{/* {renderedElements} */}
									<div dangerouslySetInnerHTML={{ __html: getDisplayText() }} />
									{/* <Typography variant="body1">
										{getDisplayText()}....
									</Typography> */}
									{(filterString && filterString).split("").length >
										94 && (
										<div>
											{!isFullTextVisible ? (
												<Button onClick={() => setIsFullTextVisible(true)}>
													Read More
												</Button>
											) : (
												<Button onClick={() => setIsFullTextVisible(false)}>
													Read Less
												</Button>
											)}
										</div>
									)}
								</Grid>

								
							</Grid>
						</Grid>
					</Grid>
				</div>
			</Drawer>
		</>
	);
};

export default ServiceplanDetail;
